/*GENERAL*/
body {
  font-family: "Roboto", serif; }

/*HEPLERS*/
.main_content {
  width: 1203px;
  display: table;
  margin: auto; }

.full_screen {
  display: table;
  width: 100%; }

.full {
  display: none; }

.left {
  float: left; }

.right {
  float: right; }

/*BUTTONS*/
.exit_btn {
  color: red;
  background: blue;
  font-size: 18px;
  border-color: blue; }
  .exit_btn:hover, .exit_btn:focus, .exit_btn.focus {
    color: red;
    background: #0000cc;
    border-color: #0000c2; }

.auth_btn {
  color: red;
  background: blue;
  font-size: 18px;
  border-color: blue; }
  .auth_btn:hover, .auth_btn:focus, .auth_btn.focus {
    color: red;
    background: #0000cc;
    border-color: #0000c2; }

.submit_btn {
  color: yellow;
  background: green;
  font-size: 18px;
  border-color: green;
  margin-top: 5%; }
  .submit_btn:hover, .submit_btn:focus, .submit_btn.focus {
    color: yellow;
    background: #004d00;
    border-color: #004300; }

/*FLASH MESSAGES*/
/*MESSAGE*/
.message {
  width: 1132px;
  position: absolute;
  top: 255px;
  font-size: 21px;
  margin-left: 19px;
  z-index: 101; }

.alert {
  padding: 2px; }

.message_text {
  display: table;
  margin: auto;
  text-align: center; }

.close {
  margin-top: 6px;
  margin-right: 30px; }

.close_message {
  position: absolute;
  right: -5px; }

/*MODAL*/
/*HEADER*/
.container_header {
  width: 1098px;
  display: table;
  margin: auto;
  padding: 25px 20px 25px 20px; }
  .container_header .logo {
    display: inline-block;
    float: left;
    background-color: #fff; }
  .container_header .descript {
    float: left;
    display: inline-block;
    overflow: hidden;
    color: #073559;
    font-size: 24px;
    font-weight: 400;
    margin-top: 50px;
    max-width: 300px; }
    .container_header .descript p {
      margin-bottom: 0; }
  .container_header .contacts {
    display: inline-block;
    color: #212121;
    font-size: 24px;
    margin-top: 50px;
    margin-left: 45px; }
    .container_header .contacts p {
      font-weight: 400;
      display: inline-block;
      vertical-align: bottom; }
    .container_header .contacts i {
      color: #073559;
      vertical-align: super;
      margin-left: 15px; }
  .container_header .socials {
    float: right;
    padding-top: 10px; }
    .container_header .socials i {
      color: #212121;
      margin-left: 17px;
      margin-bottom: 10px; }
      .container_header .socials i:hover {
        color: #729521; }
    .container_header .socials .fa-heart {
      color: #073559;
      float: left;
      margin: 60px 0px 0 0; }
      .container_header .socials .fa-heart:hover {
        color: #073559; }
    .container_header .socials .counter {
      font-size: 20px;
      margin-left: 80px; }
      .container_header .socials .counter p {
        margin-bottom: 0; }
      .container_header .socials .counter a {
        color: inherit;
        text-decoration: underline; }

/*NAVBAR*/
.container_navbar {
  background-color: #073559;
  -webkit-box-shadow: 0px 1px 3px black;
  box-shadow: 0px 1px 3px black; }

.navbar-default {
  background-color: #073559;
  border-color: #073559;
  display: table;
  margin: auto;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 300; }
  .navbar-default .navbar-brand {
    color: blue; }
    .navbar-default .navbar-brand:hover, .navbar-default .navbar-brand:focus {
      color: black;
      background-color: purple; }
  .navbar-default .navbar-text {
    color: white; }
  .navbar-default .navbar-nav > li > a {
    color: white; }
    .navbar-default .navbar-nav > li > a:hover, .navbar-default .navbar-nav > li > a:focus {
      color: #fff;
      background-color: #0e6db8; }
  .navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:hover, .navbar-default .navbar-nav > .active > a:focus {
    color: #fff;
    background-color: #0e6db8; }
  .navbar-default .navbar-link {
    color: white; }
    .navbar-default .navbar-link:hover {
      color: #fff; }
  .navbar-default .btn-link {
    color: white; }
    .navbar-default .btn-link:hover, .navbar-default .btn-link:focus {
      color: #fff; }

/*LEFT SIDEBAR*/
/*RIGHT SIDEBAR*/
/*MAIN PAGE*/
.main {
  background-color: #729521;
  margin-top: 1px; }

#carousel_estate {
  width: 1203px;
  display: table;
  margin: auto; }
  #carousel_estate .carousel-inner .item img {
    display: table;
    margin: auto; }
  #carousel_estate .carousel-inner .item .carousel-caption {
    text-align: right;
    top: -1%;
    right: 7%;
    color: #212121;
    font-weight: 200;
    text-shadow: none; }
    #carousel_estate .carousel-inner .item .carousel-caption h3 {
      margin-top: 10px; }
  #carousel_estate .fa {
    position: absolute;
    top: 50%; }

/*CATALOG*/
.catalog_blocks {
  width: 100%;
  overflow: auto;
  background-color: #fff; }

.one_item {
  background-color: #eee;
  margin-bottom: 5%;
  padding: 10px 15px 25px 15px;
  margin-left: 6%; }
  .one_item .img {
    background-color: #fff;
    max-width: 265px;
    max-height: 265px;
    min-width: 265px;
    min-height: 265px;
    display: table;
    margin: auto;
    position: relative; }
    .one_item .img:before, .one_item .img:after {
      content: " ";
      display: table; }
    .one_item .img:after {
      clear: both; }
  .one_item .item_img {
    min-width: 265px;
    max-width: 265px;
    max-height: 265px;
    display: table;
    margin: auto; }
  .one_item .item_title {
    text-align: center;
    font-weight: bold;
    font-size: 14px;
    text-transform: uppercase;
    overflow: hidden; }
    .one_item .item_title a {
      color: inherit;
      cursor: pointer; }
      .one_item .item_title a:hover {
        text-decoration: none; }
  .one_item .short_descr {
    position: relative; }
    .one_item .short_descr .item_descr {
      overflow: hidden;
      max-height: 100px;
      min-height: 100px; }
      .one_item .short_descr .item_descr:before {
        content: "";
        display: block;
        position: relative;
        z-index: 2;
        height: 20px;
        top: 80px;
        background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), #eee 75%); }
      .one_item .short_descr .item_descr:hover + .full {
        display: block;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 10;
        background-color: #eee;
        height: auto;
        width: auto;
        max-height: initial; }
        .one_item .short_descr .item_descr:hover + .full:before {
          visibility: hidden; }
    .one_item .short_descr .full:hover {
      display: block;
      position: absolute;
      top: 0px;
      left: 0px;
      z-index: 10;
      background-color: #eee;
      height: auto;
      width: auto;
      max-height: initial; }
      .one_item .short_descr .full:hover:before {
        visibility: hidden; }
  .one_item .item_descr {
    font-size: 14px;
    color: #717171; }
  .one_item .price {
    overflow: hidden;
    display: table;
    margin: auto; }
  .one_item .item_price {
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    float: left; }
  .one_item .item_currency {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    float: right;
    text-transform: uppercase;
    margin-top: 12px;
    margin-left: 10px; }
  .one_item .y_btn {
    text-transform: uppercase;
    display: table;
    margin: auto; }

.more_item {
  margin-bottom: 5%;
  font-size: 25px; }
  .more_item .fa {
    margin-left: 5%;
    margin-right: 5%;
    display: none; }

/*FOOTER*/
.full_screen {
  background-color: #505050; }

footer {
  padding: 15px 5px 15px 6px;
  overflow: hidden;
  width: 1098px;
  display: table;
  margin: auto;
  color: #fff; }

.logo_footer {
  float: left;
  margin-right: 20px; }

.footer_descript {
  max-width: 250px; }
  .footer_descript p {
    margin-bottom: 0;
    font-weight: 200; }

.footer_nav_block {
  display: inline-block; }
  .footer_nav_block ul {
    margin-right: 30px;
    margin-bottom: 0; }
    .footer_nav_block ul:last-child {
      margin-bottom: 0; }
    .footer_nav_block ul li {
      margin-bottom: 8px; }
      .footer_nav_block ul li a {
