.copyright {
  display: block;
  float: right;
  font-weight: 200;
  margin-right: 20px;
  margin-top: 27px; }
  .copyright .our_link {
    float: right; }
    .copyright .our_link p {
      color: #fff; }
    .copyright .our_link a {
      color: #9ccb49; }
      .copyright .our_link a:hover {
        color: #13b4e6;
        text-decoration: none; }
      .copyright .our_link a:active, .copyright .our_link a:focus {
        outline: none;
        text-decoration: none; }
